.search-bar {
  display: flex;
  align-items: center;
  background: #fff;
  border-radius: 32px;
  box-shadow: 0 2px 12px rgba(0,0,0,0.06);
  padding: 6px 12px 6px 24px;
  max-width: 520px;
  margin: 0 auto;
  position: relative;
}
.search-input {
  border: none;
  outline: none;
  font-size: 1.1rem;
  flex: 1;
  background: transparent;
  padding: 10px 0;
}
.search-btn {
  background: #fff;
  border: none;
  border-radius: 50%;
  width: 40px;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1.3rem;
  cursor: pointer;
  margin-left: 8px;
  transition: background 0.2s;
}
.search-btn:hover {
  background: #f2f2f2;
} 