.job-detail-container {
  max-width: 1000px;
  margin: 48px auto 0 auto;
  background: #fff;
  border-radius: 16px;
  box-shadow: 0 2px 12px rgba(0,0,0,0.06);
  padding: 40px 48px 36px 48px;
  position: relative;
}
.back-btn {
  position: absolute;
  left: 24px;
  top: 24px;
  background: none;
  border: none;
  color: #888;
  font-size: 1.1rem;
  cursor: pointer;
  transition: color 0.2s;
}
.back-btn:hover {
  color: #222;
}
.job-detail-title {
  font-size: 2rem;
  font-weight: 700;
  margin-bottom: 18px;
  color: #222;
}
.job-detail-meta {
  color: #888;
  font-size: 1.05rem;
  margin-bottom: 24px;
}
.job-detail-desc {
  color: #444;
  font-size: 1.13rem;
  line-height: 1.8;
  margin-top: 10px;
  margin-bottom: 32px;
}

/* 新增分块样式 */
.job-section {
  margin-bottom: 32px;
}
.job-section-title {
  font-size: 1.25rem;
  font-weight: 700;
  margin-bottom: 12px;
  color: #222;
}
.job-section-list {
  padding-left: 0;
  margin: 0;
  list-style: none;
}
.job-section-list li {
  color: #444;
  font-size: 1.08rem;
  line-height: 1.8;
  margin-bottom: 4px;
}
.job-section-paragraphs p {
  color: #444;
  font-size: 1.08rem;
  line-height: 1.8;
  margin-bottom: 10px;
}

.job-section-email {
  margin-top: 40px;
  padding: 24px;
  background: #f8f9fa;
  border-radius: 12px;
  text-align: center;
}
.job-section-email-tip {
  font-size: 1.1rem;
  color: #444;
  margin-bottom: 12px;
}
.job-section-email-address {
  font-size: 1.2rem;
  color: #222;
  font-weight: 500;
  margin-bottom: 16px;
}

.apply-link {
  display: inline-block;
  padding: 12px 32px;
  background: #ffd400;
  color: #222;
  text-decoration: none;
  border-radius: 8px;
  font-weight: 600;
  font-size: 1.1rem;
  transition: all 0.2s ease;
}

.apply-link:hover {
  background: #ffdc33;
  transform: translateY(-2px);
  box-shadow: 0 4px 12px rgba(255, 212, 0, 0.2);
} 