.navbar {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 20px 40px;
  background: transparent;
  box-shadow: none;
}

.navbar-logo {
  display: flex;
  align-items: center;
}
.logo-icon {
  background: #ffd400;
  color: #222;
  font-weight: bold;
  border-radius: 4px;
  padding: 4px 8px;
  margin-right: 10px;
}
.logo-img {
  height: 44px;
  width: auto;
  display: inline-block;
  vertical-align: middle;
  margin-right: 0;
}
.logo-text {
  font-size: 1.2rem;
  font-weight: 600;
  color: #fff;
}

.navbar-menu {
  display: flex;
  list-style: none;
  gap: 30px;
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
}
.navbar-menu li a {
  text-decoration: none;
  color: #fff;
  font-size: 1rem;
  font-weight: 600;
  padding-bottom: 2px;
  border-bottom: 2px solid transparent;
  transition: border 0.2s;
}
.navbar-menu li a.active, .navbar-menu li a:hover {
  border-bottom: 2px solid #ffd400;
}

.navbar-lang .lang-btn {
  background: none;
  border: 1px solid #fff;
  border-radius: 6px;
  padding: 4px 12px;
  font-size: 0.95rem;
  cursor: pointer;
  color: #fff;
  transition: border 0.2s;
}
.navbar-lang .lang-btn:hover {
  border: 1.5px solid #ffd400;
}

/* 语言下拉菜单样式 */
.lang-dropdown {
  position: absolute;
  right: 0;
  top: 110%;
  background: #fff;
  border: 1px solid #eee;
  border-radius: 8px;
  box-shadow: 0 2px 8px rgba(0,0,0,0.08);
  min-width: 120px;
  z-index: 10;
}
.lang-option {
  padding: 10px 18px;
  cursor: pointer;
  color: #222;
  font-size: 1rem;
  transition: background 0.2s;
}
.lang-option:hover {
  background: #f7f7f7;
} 