.banner-bg {
  background: url('https://s3plus.meituan.net/banma-deliveryai/header%20people%203840%20960.jpg') center/cover no-repeat;
  min-height: 320px;
  width: 100%;
}
.banner {
  min-height: 320px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding-top: 60px;
  padding-bottom: 60px;
  position: relative;
}
.banner-title {
  color: #fff;
  font-size: 3.6rem;
  font-weight: 700;
  margin-bottom: 36px;
  text-shadow: 0 2px 8px rgba(0,0,0,0.18);
}

.job-list-section {
  max-width: 900px;
  margin: 0 auto;
  padding: 40px 0 60px 0;
}
.job-list-title {
  font-size: 1.4rem;
  font-weight: 600;
  margin-bottom: 30px;
  color: #222;
}
.job-count {
  color: #ffd400;
  font-size: 1.1rem;
}
.job-list {
  display: flex;
  flex-direction: column;
  gap: 24px;
} 