.job-card {
  background: #fff;
  border-radius: 16px;
  box-shadow: 0 2px 12px rgba(0,0,0,0.06);
  padding: 28px 32px 20px 32px;
  cursor: pointer;
  transition: box-shadow 0.2s, transform 0.2s;
  width: 100%;
  min-width: 0;
}
.job-card:hover {
  box-shadow: 0 6px 24px rgba(0,0,0,0.10);
  transform: translateY(-2px) scale(1.01);
}
.job-title {
  font-size: 1.3rem;
  font-weight: 600;
  margin-bottom: 10px;
  color: #222;
}
.job-meta {
  color: #888;
  font-size: 0.98rem;
  margin-bottom: 12px;
}
.job-desc {
  color: #444;
  font-size: 1.05rem;
  line-height: 1.6;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.job-card.expanded {
  box-shadow: 0 4px 16px rgba(0, 0, 0, 0.12);
} 