.footer {
  background: #fafbfc;
  border-top: 1.5px solid #e5e6eb;
  margin-top: 60px;
  padding-top: 32px;
  padding-bottom: 32px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.footer-main {
  display: flex;
  align-items: center;
  justify-content: center;
  max-width: 1200px;
  margin: 0 auto 0 auto;
  padding: 0 40px;
  width: 100%;
}
.footer-logo {
  display: flex;
  align-items: center;
  font-size: 2rem;
  font-weight: 700;
  margin-bottom: 12px;
}
.footer-logo-icon {
  background: #ffd400;
  color: #222;
  font-weight: bold;
  border-radius: 6px;
  padding: 4px 10px;
  margin-right: 12px;
  font-size: 1.2rem;
}
.footer-logo-text {
  color: #222;
  font-size: 1.5rem;
  font-weight: 700;
}
.footer-bottom {
  display: flex;
  align-items: center;
  justify-content: space-between;
  max-width: 1200px;
  margin: 0 auto;
  padding: 18px 40px 0 40px;
  font-size: 1rem;
  width: 100%;
}
.footer-links {
  color: #888;
  display: flex;
  align-items: center;
  gap: 8px;
  flex-wrap: wrap;
}
.footer-links a {
  color: #888;
  text-decoration: underline;
  margin: 0 2px;
}
.footer-divider {
  margin: 0 8px;
  color: #d0d0d0;
}
.footer-social {
  display: flex;
  align-items: center;
  gap: 8px;
  color: #888;
  font-size: 1.05rem;
}
.footer-linkedin {
  display: flex;
  align-items: center;
  margin-left: 6px;
  transition: transform 0.2s;
}
.footer-linkedin:hover {
  transform: scale(1.08);
} 